import React from "react";
import styled, { ThemeProvider } from "styled-components";

import langs from "langs";

import Nav from "components/Nav";

const El = styled.div`
  min-height: 100vh;
  padding-top: 25px;
  padding-bottom: 65px;

  transition: background 500ms ease;

  ${({ theme }) => `
    background: linear-gradient(${theme.background[0]}, ${theme.background[1]});
  `};
`;

const Text = styled.h1`
  font: 135px/125px "Delius Swash Caps", Georgia, serif;
  margin: 0;
  padding: 0;
  line-height: 125px;
  letter-spacing: -3px;
  cursor: default;
  color: #fff;
  width: ${(props) => (props.lang === "german" ? "800px" : "700px")};
  height: 500px;
  margin: 0 auto;
  text-shadow: 0 2px 0 ${({ theme }) => theme.shadow};
`;

const App = ({ lang }) => (
  <ThemeProvider
    theme={{
      ...langs[lang].colors,
      lang,
    }}
  >
    <El>
      <Text lang={lang}>{langs[lang].text}</Text>
      <Nav lang={lang} />
    </El>
  </ThemeProvider>
);

App.defaultProps = {
  lang: "english",
};

export default App;



